*
{
    margin:0;
    padding:0;
}

.header{
    text-align: center;
    color: #f71aa2;
  }
  body{
    background: url("http://superdevresources.com/wp-content/uploads/2014/02/pattern-background.png");
    height: 100vh;
    font-family: 'Times New Roman', Times, serif;
    display: flex;
    justify-content: center;
    align-items: center;

  }
  .app
  {
    background: linear-gradient(83deg,#f71aa2 0%, #6800ff 80%);
    width: 95%;
    min-height: 200px;
    border-radius: 15px;
    padding: 20px;
    box-shadow: 10px 10px 42px 0px rgba(0, 0, 0, 0.75);
    display: flex;
    justify-content: space-evenly;
  }
  .score-section
  {
    display: flex;
    align-items: center;
    font-size: 30px;
    color:white;
    font-weight: bold;
  }
  .Question-section
  {
    position: relative;
    width: 100%;
  }
  .Question-Count{
    margin-bottom: 20px;
    color:darkorange;
  }

  .Question-Count span{
    font-size: 40px;
  } 

  .Question-Text
  {
    color:white;
  font-size: 30px;
  font-weight: bold;
  }
  .Answer-Section
  {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 100%;
  }
  .Answer-Section button
  {
    width: 100%;
  font-size: 16px;
  color: #ffffff;
  background: linear-gradient(83deg,#f71aa2 0%, #6800ff 80%);
  border-radius: 15px;
  display: flex;
  padding:10px;
    justify-content: flex-start;
  align-items: center;
  border: 3px solid #234668;
  cursor: pointer;
  }